import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';  
import { map } from 'rxjs/operators';

import {environment} from '../../environments/environment';
import { Constantes } from '../core/util/constantes';
 
@Injectable({providedIn: 'root'})
export class UsuarioService {
 


    constructor(private http: HttpClient) {
     }

    /***************************************************************************************************
     * RESOURCE SISTEMA
     ***************************************************************************************************/
    private urlSistema(): string {
        return `${environment.corporativo_url}/sistemas/4`;
    }   

    /***************************************************************************************************
     * RESOURCE usuarios cadastrados no scaf
     ***************************************************************************************************/
    private urlUsuarioSistema(codigo?: number): string {
        return `${this.urlSistema()}/usuarios${codigo > 0 ? '/' + codigo : ''}`;
    }  

    /***************************************************************************************************
     * RESOURCE usuarios cadastrados no corporativo
     ***************************************************************************************************/
    private urlUsuarioCorporativo(codigo?: any): string {
        return `${environment.corporativo_url}/usuarios${codigo > 0 ? '/' + codigo : ''}`;
    }  


    public findPerfis(): Observable<any[]> {
        return this.http.get<any[]>(`${this.urlSistema()}/perfis`);
    }

    public find(filtro:any): Observable<any[]> {
        let options = {params:filtro};
        return this.http.get<any[]>(this.urlUsuarioSistema(), options);
    }

    public findById(codigo: number): Observable<any[]> {
        return this.http.get<any[]>(this.urlUsuarioSistema(codigo));
    }

    public save(objeto: any): Observable<any> {
        if (objeto.codigo > 0) {
            objeto.id = objeto.codigo;
            return this.http.put<any>(this.urlUsuarioSistema(objeto.codigo), objeto);
        } else {
            return this.http.post<any>(this.urlUsuarioSistema(), objeto); 
        }
    }

    public alterarStatus(objeto:any, status:string): Observable<any> { 
        return this.http.put<any>(this.urlUsuarioSistema(objeto.codigo)+'/status/'+status, objeto);
    } 

    public remove(codigo: number): Observable<any> {
        return this.http.delete<any>(this.urlUsuarioSistema(codigo));
    }   


    public findUsuariosCorporativo(filtro:any): Observable<any[]> {
        let options = {params:filtro};
        return this.http.get<any[]>(this.urlUsuarioCorporativo(), options);
    }

    public findUsuarioCorporativoByLogin(login: string): Observable<any> {
        return this.http.get<any[]>(this.urlUsuarioCorporativo(login)+"/");
    }

    public findFotoUsuarioCorporativo(codigo: number): Observable<any[]> {
        return this.http.get<any[]>(this.urlUsuarioCorporativo(codigo)+"/foto");
    }

    public saveFotoUsuarioCorporativo(codigo: number, foto:string): Observable<any[]> {
        let fotoVO = {foto:foto};
        return this.http.put<any[]>(this.urlUsuarioCorporativo(codigo)+"/foto", fotoVO);
    }


    /***************************************************************************************************
     *  retorna o usuário logado 
     ***************************************************************************************************/
    public getUsuario(): any {
        let s = localStorage.getItem(Constantes.USUARIO);
        if (s && s != '') {
            return JSON.parse(s);
        }
        return {};
    } 

    /***************************************************************************************************
     *  retorna o código do usuário logado
     ***************************************************************************************************/
    public getIdUsuario(): any { 
        return this.getUsuario().codigo;
    } 
    
    /***************************************************************************************************
     *  seta o usuário logado 
     ***************************************************************************************************/
    public setUsuario(access_user:any) {
        localStorage.setItem(Constantes.ACCESS_USER, JSON.stringify(access_user)); 
        localStorage.setItem(Constantes.USUARIO, JSON.stringify(access_user.usuario)); 
        localStorage.setItem(Constantes.REFRESH_TOKEN, access_user.refresh_token);
        localStorage.setItem(Constantes.ACCESS_TOKEN, `${access_user.token_type} ${access_user.access_token}`);
    } 

}